
import { defineComponent } from "vue";
import LinkTo from "@/components/LinkTo.vue";

export default defineComponent({
  props: {
    // link target
    to: String,
    // font awesome icon code
    icon: String,
    // text to display
    text: String,
    // plain, slim, or big
    design: String
  },
  components: {
    LinkTo
  },
  data() {
    return {
      // whether to render <a> (router link) or <button>
      component: this.to ? "LinkTo" : "button"
    };
  }
});
