
import { defineComponent } from "vue";
import LinkTo from "@/components/LinkTo.vue";

export default defineComponent({
  components: {
    LinkTo
  },
  computed: {
    // user's avatar
    avatar() {
      return this.$store.state.avatar || "";
    },
    // user's full name
    name() {
      return this.$store.state.name;
    },
    // user's initials
    userInitials() {
      return this.$store.state.name
        .split(/\s/)
        .filter((w: string) => w)
        .map((w: string) => w[0])
        .slice(0, 2)
        .join("")
        .toUpperCase();
    }
  }
});
